.container {
  position: relative;
  top: 0;
  left: 0;
  width: 35px;
  height: 35px;
  margin-left: auto;
}

.counter {
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  font-size: 13px;
  font-weight: 600;
  color: black;
  text-decoration: none;
  background-color: white;
  border-radius: 50%;
}

.icon {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

@media screen and (max-width: 710px) {
  .container {
    display: none;
  }
}
